const headers = [{
		            field: "name"
	            },
	            {
		            field: "role",  cellClass:"rolefor"
	            },
	            {
		            field: "email", cellClass:"smaller", cellTooltip: (row, col) => {
		            	return 'Email: '+ row.entity.name;
		            }
	            },
	            {
		            field: "details", cellTooltip: (row, col) => {
		            	return 'This is book written by: '+ row.entity.name;
		            }
	            },
	            {
		            name: 'userOption',
		            enableSorting: false,
	                displayName: 'User Option', 
	                cellTemplate: `<button id="editBtn" type="button"  ng-click="editUser(row.entity)" >Edit</button>
	     <button id="delBtn" type="button"  ng-click="deleteUser(row.entity)" >Delete</button> `
	        }];
export default headers;


