//import db from './data';
import headers from './headers';
import firebase from 'firebase';


class GridController {
	static $inject = ['$firebaseObject','$firebaseArray'];
	constructor($firebaseObject,$firebaseArray) {
     this.name = "ui-grid-controller";
	const rootRef = firebase.database().ref();
	this.users = $firebaseArray(rootRef);
	let db = this.users;  

		this.usersTable = {
			infiniteScrollRowsFromEnd: 21,
            infiniteScrollUp: true,
            infiniteScrollDown: true,
			enableSorting: true,
			enableFiltering: true,
			showGridFooter: true,
			enableGridMenu: true,
			data: db,
	        columnDefs: headers
		};
		this.addNew = () => {
            db.$add({
                'name': 'new name',
                'role': 'guest',
                'email': 'no-email',
                'details': 'no details'
                });
		}

	}
}
   
export default GridController;



