class NavbarController {
  constructor() {
    this.name = 'navbar';
    this.links =[
      {name: 'Home', link : "home", id:1},
      {name: 'Search',    link: "search", id:2   },
      {name: 'User List', link : 'userlist',id:3},
      {name: 'Statistic', link : "statistic", id:4}
     ];
    this.config={
     active: "col-sm-3  active__item",
     passive: "col-sm-3 passive__item"
    };
    this.checkActive = function (selected){
    this.links.forEach(item =>
       item.name == selected.name? item.current = true : item.current = false);
      };
    this.access = function(){return !!localStorage.length};
}};
export default NavbarController;



