class LoginController {
  constructor($state) {
   let vm = this;
    vm.formData = {
      userName : '',
      userPassword : ''
    };
    
    vm.submit = function() {
      if (vm.formData.userName === "admin" && vm.formData.userPassword === "admin") {
        localStorage.setItem("userName",vm.formData.userName);
        $state.go('home');
      } else if (vm.formData.userName === "" || vm.formData.userPassword === "") {
        vm.submitted = true;
      } else {
        vm.error = true;
      }
    }
  }
}
LoginController['$inject'] = ['$state'];
export default LoginController;



