class StatisticController {
  constructor() {
    this.name = 'Statistic';
	
	this.progresschart = {
		id: 'myFirstChart',
		height: '300px',
		background:'#E1E8ED',
		width: '300px',
		amChartOptions: {
        data:[ {
    "country": "Germany",
    "visits": 1322
  }, {
    "country": "UK",
    "visits": 1122
  }, {
    "country": "France",
    "visits": 1114
  }, {
    "country": "India",
    "visits": 984
  }, {
    "country": "Spain",
    "visits": 711
  }, {
    "country": "Netherlands",
    "visits": 665
  }, {
    "country": "South Korea",
    "visits": 443
  }, {
    "country": "Canada",
    "visits": 441
  }],
   "type": "serial",
  "theme": "light",
   "valueAxes": [ {
    "gridColor": "#0000FF",
    "gridAlpha": 0.2,
    "dashLength": 0
  } ],
  "gridAboveGraphs": true,
  "startDuration": 1,
  "graphs": [ {
    "balloonText": "[[category]]: <b>[[value]]</b>",
    "fillAlphas": 0.8,
    "lineAlpha": 0.2,
    "type": "column",
    "valueField": "visits"
  } ]
    }
};
	this.piechart = {
		id: "mySecondChart",
		height: '400px',
		background:'#F7F7F7',
		width: '500px',
		amChartOptions: {
        data: [ {
    "country": "Ireland",
    "litres": 201.1
  }, {
    "country": "Germany",
    "litres": 165.8
  }, {
    "country": "Australia",
    "litres": 139.9
  }, {
    "country": "Austria",
    "litres": 128.3
  }, {
    "country": "UK",
    "litres": 99
  }, {
    "country": "Belgium",
    "litres": 160
  }, {
    "country": "The Netherlands",
    "litres": 150
  } ],
        type: "pie",
        theme: 'light',
		 valueField: "litres",
  titleField: "country",
   balloon:{
   fixedPosition:true
        }
    } 
};
	this.profitschart = {
		id: "myThirdChart",
		height: '200px',
		background:'#f3e7d4',
		width: '450px',
		amChartOptions: {
        data: [{
            year: 2005,
            income: 23.5,
            expenses: 18.1
        }, {
            year: 2006,
            income: 26.2,
            expenses: 22.8
        }, {
            year: 2007,
            income: 30.1,
            expenses: 23.9
        }, {
            year: 2008,
            income: 29.5,
            expenses: 25.1
        }, {
            year: 2009,
            income: 24.6,
            expenses: 25
        }],
        type: "serial",
        theme: 'black',
        categoryField: "year",
        rotate: true,
        pathToImages: 'https://cdnjs.cloudflare.com/ajax/libs/amcharts/3.13.0/images/',
        legend: {
            enabled: true
        },
        chartScrollbar: {
            enabled: true,
        },
        categoryAxis: {
            gridPosition: "start",
            parseDates: false
        },
        valueAxes: [{
            position: "top",
            title: "Million smiles"
        }],
        graphs: [{
            type: "column",
            title: "Income",
            valueField: "income",
            fillAlphas: 1,
        }]
    }
	};
  }
}

export default StatisticController;



